.reservation__form {
  background-color: $color-tangaroa;
  padding: 25px 50px 50px;

  @include vp-1023 {
    margin: 0;
    margin-left: 8px;
    margin-right: 9px;
    padding: 26px 50px 50px;
  }

  @include vp-767 {
    padding: 17px 30px 40px;
  }
}

.reservation__form h3 {
  @include font($ff-montserrat, 500, 18px, $color-alice-blue, 150%);
  margin-bottom: 28px;

  @include vp-1023 {
    margin-bottom: 27px;
  }

  @include vp-767 {
    text-align: center;
    line-height: 140%;
    margin-bottom: 28px;
  }
}

.reservation__form form {
  max-width: 403px;

  @include vp-1023 {
    max-width: none;
  }
}

.reservation__form input {
  @include font($ff-montserrat, 400, 14px, $color-tangaroa, 150%);
  display: block;
  background-color: $color-alice-blue;
  border: none;
  width: 100%;
  min-height: 43px;
  margin-bottom: 20px;
  padding: 11px 16px;

  &::placeholder {
    color: rgba($color-tangaroa, 0.7);
  }

  @include vp-1023 {
    padding: 4px 16px;
  }
}

.reservation__form-agreement {
  position: relative;
  min-height: 18px;
  margin-bottom: 28px;
  margin-top: 22px;

  @include vp-1023 {
    margin-top: 23px;
  }

  @include vp-767 {
    margin-top: 3px;
    margin-bottom: 26px;
  }
}

.reservation__form-agreement label {
  display: flex;
  align-items: center;
  cursor: pointer;

  @include vp-767 {
    align-items: stretch;
  }
}

.reservation__form-agreement-check-mark {
  position: absolute;
  content: "";
  width: 18px;
  height: 18px;
  background-color: $color-alice-blue;

  @include vp-767 {
    width: 20px;
    height: 20px;
  }
}

.reservation__form-agreement input:checked + .reservation__form-agreement-check-mark {
  background-image: url("../img/svg/agree.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.reservation__form-agreement-description {
  display: block;
  align-self: center;
  padding-left: 23px;
  color: $color-alice-blue;
  font-family: $ff-montserrat;
  font-size: 10px;
  font-weight: 400;
  line-height: 130%; /* 13px */

  @include vp-767 {
    padding-left: 28px;
    line-height: 120%;
  }
}

.reservation__form-button {
  border: none;
  width: 100%;
  padding-top: 10px;
  padding-bottom: 12.4px;
  background-color: $color-alice-blue;
  color: $color-tangaroa;
  font-family: $ff-montserrat;
  font-size: 14px;
  font-weight: 600;
  line-height: 150%;

  &:hover,
  &:focus,
  &:active {
    cursor: pointer;
    box-shadow: 0 0 30px rgba($color-default-white, 0.7);
  }

  @include vp-1023 {
    padding-top: 11px;
    padding-bottom: 9px;
  }

  @include vp-767 {
    padding-bottom: 11px;
  }
}
