@font-face {
  font-family: "Montserrat";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.wrapper__header {
  position: relative;
  min-height: 650px;
}
@media (max-width: 1023px) {
  .wrapper__header {
    min-height: 638px;
    margin-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .wrapper__header {
    min-height: 500px;
    margin-bottom: 10px;
  }
}

.header {
  padding: 32px 40px 0 71px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 1023px) {
  .header {
    padding: 24px 22px;
  }
}
@media (max-width: 767px) {
  .header {
    flex-wrap: wrap;
    padding: 15.5px 0 17px;
  }
}

.header__logo {
  margin-top: 3px;
}
@media (max-width: 1023px) {
  .header__logo {
    margin-top: 5px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    margin-top: 4px;
  }
}

@media (max-width: 767px) {
  .header.is-active {
    background-color: #f9fbfd;
    position: fixed;
    z-index: 100;
    align-items: flex-start;
    right: 0;
    top: 0;
    bottom: 0;
    height: 100%;
    width: 88.8%;
    padding-left: 15px;
    padding-right: 15px;
  }
  .header.is-active::before {
    content: "";
    position: absolute;
    top: 0;
    right: 100%;
    bottom: 0;
    width: 100%;
    background-color: rgba(1, 28, 64, 0.5);
    pointer-events: none;
  }
  .header.is-active svg {
    fill: #011c40;
    margin-bottom: 31.5px;
  }
  .header.is-active + .intro h1 {
    padding-top: 68px;
    padding-bottom: 78px;
  }
}

@media (max-width: 767px) {
  .header.is-active + .intro h1 {
    padding-top: 68px;
    padding-bottom: 0;
  }
}

.header__button-burger {
  padding: 0;
  margin-bottom: 3px;
  background-color: transparent;
  z-index: 10;
  display: none;
}
@media (max-width: 767px) {
  .header__button-burger {
    display: block;
    width: 24px;
    height: 14px;
    background-image: url("../img/svg/icon-burger.svg");
    border: none;
    cursor: pointer;
  }
}

.header.is-active .header__button-burger {
  display: none;
}
@media (max-width: 767px) {
  .header.is-active .header__button-burger {
    width: 17px;
    display: block;
    margin-top: 6px;
    height: 25px;
    background: url("../img/svg/icon-close.svg") no-repeat;
    border: none;
  }
}

@media (max-width: 767px) {
  .header__menu {
    height: 0;
    width: 100%;
    overflow: hidden;
  }
}

.header.is-active .header__menu {
  width: 100%;
  height: 100%;
  overflow-y: auto;
}

.header__menu-list.menu-list {
  margin: 0;
